@import url("https://fonts.googleapis.com/css2?family=Inter:ital,opsz,wght@0,14..32,100..900;1,14..32,100..900&display=swap");

* {
  padding: 0;
  margin: 0;
  font-family: Inter, sans-serif;
}

.app {
  width: 100%;
  height: 100vh;
  background-color: #1A1A1C;
  color:#FFFFFF;
;
  display: grid;
  grid-template-rows: 2fr 10fr;
}

.app > .input-container {
  /* background-color: orange; */
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  padding-inline:2rem ;
}

.input-container>.searchField{
flex: 1;
padding: 1rem;
background: transparent;
color: #FFFFFF;
font-size: 16px;
border: 1px solid #FFFFFF;

&::placeholder{
  color: #FFFFFF;
  font-size: 16px;
}
}

.input-container>.sortBtn{
width: 12rem;
padding: 0.8rem;

color: #FFFFFF;
/* border: none; */
background-color: transparent;
border: 1px solid #FFFFFF;
font-size: 16px;
font-weight: 400;
line-height: 29.05px;
text-align: center ;
cursor: pointer;

&:active{
  transform: scale(0.8);
}
}

.app > .dashboard {
  /* background-color: orangered; */
}

.app > .dashboard > .dashTable {
  /* background-color: aquamarine; */
  width: 100%;
  height: 100%;
  padding-block: 0.8rem;
  border: 1px solid black;
  border-collapse: collapse; /* Ensure borders collapse into a single border */
}

.dashboard th, .dashboard td {
  padding: 8px;
  white-space: nowrap; 
  overflow: hidden; /* Hide overflowed text */
  text-overflow: ellipsis; /* Add ellipsis for overflowed text */

}

.dashboard th{
  border: 1px solid #8E8E8E;
}


tbody {
  /* background-color: red; */
  height: 100%; /* Set fixed height for tbody */
  overflow-y: scroll; /* Allow vertical scrolling */
  display: block; /* Make tbody a block element */
  &::-webkit-scrollbar{
    display: none;
  }
  outline: none;
}

tr {
  height: 50px; /* Fixed height for each row */
  /* background-color: orange; */
  display: table; /* Restore table row behavior */
  width: 100%;
  table-layout: fixed; /* Ensure cells take equal width */
  border-bottom: 1px solid #8E8E8E;
}

td {
  height: 50px; /* Ensure td height is consistent with tr height */
  padding: 8px;
  vertical-align: middle; /* Center align content vertically */
  /* background-color: yellow; */
  text-align: center;
}

.nameContainer{
  display: flex;
  gap: 1rem;
  align-items: center;
}

img {
  width: 1rem;
}

.red_color{
  color: red;
}
.green_color{
  color: rgb(12, 224, 12);
}